          </b-col>
        </div>

        </div>
    </div>
</template>
<script>
import Swal from 'sweetalert2'
import { mapGetters, mapActions } from 'vuex'
import vue2Dropzone from 'vue2-dropzone'
import 'vue2-dropzone/dist/vue2Dropzone.min.css'

export default {
  components: {
    vueDropzone: vue2Dropzone
  },
  data () {
    return {
      dropzoneOptions: {
        url: 'https://httpbin.org/post',
        thumbnailWidth: 200,
        addRemoveLinks: true
      },
      dataSkill: [],
      skill: null,
      status: localStorage.getItem('status'),
      idpekerja: localStorage.getItem('idpekerja'),
      idperekrut: localStorage.getItem('idperekrut'),
      tipepekerjaan: null,
      options: [
        { value: null, text: 'Please select an option' },
        { value: '1', text: 'Fulltime' },
        { value: '0', text: 'Freelance' }
      ],
      newPeng: {
        idpekerja: localStorage.getItem('idpekerja'),
        posisi: '',
        namaperusahaan: '',
        mulaikerja: '',
        selesaikerja: '',
        deskripsi: ''
      },
      newPor: {
        idpekerja: localStorage.getItem('idpekerja'),
        linkrepository: null,
        tipeportofolio: null,
        image1: 'doc404.jpg',
        image2: 'doc404.jpg',
        image3: 'doc404.jpg'
      },
      imageTampungan1: null,
      imageTampungan2: null,
      imageTampungan3: null,
      users: {
        dataSkill: []
      }
    }
  },
  methods: {
    // alert
    alertSuccess () {
      Swal.fire({
        icon: 'success',
        title: 'Image Updated'
      })
    },
    alertSuccessInsert () {
      Swal.fire({
        icon: 'success',
        title: 'Update Data Success'
      })
    },
    alertSuccessDelete () {
      Swal.fire({
        icon: 'success',
        title: 'Delete Data Success'
      })
    },
    alertSize () {
      Swal.fire({
        icon: 'error',
        title: 'Image size is too big!',
        text: 'Please upload another one with size < 5mb'
      })
    },
    alertValidation () {
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: 'Image type must be JPG or JPEG'
      })
    },
    alertError () {
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: 'Something went wrong!'
      })
    },
    // Pekerja
    updatePekerja () {
      const data = {
        idpekerja: this.dataz.idpekerja,
        namapekerja: this.dataz.namapekerja,
        jobdescpekerja: this.dataz.jobdescpekerja,
        domisilipekerja: this.dataz.domisilipekerja,
        tipepekerjaan: this.tipepekerjaan,
        ig: this.dataz.ig,
        github: this.dataz.github,
        gitlab: this.dataz.gitlab,
        deskripsi: this.dataz.deskripsi,
        skill: this.users.dataSkill.toString(),
        jumlahskill: this.users.dataSkill.length
      }
      this.onUpdatePekerja(data)
        .then((response) => {
          this.alertSuccessInsert()
          window.location = '/edit-profile-pekerja'
          this.getPengalaman(this.idpekerja)
        })
        .catch((err) => {
          alert(err)
        })
    },
    saveSkill () {
      this.users.dataSkill.push(this.skill)
      this.skill = ''
    },
    addPengalaman () {
      this.onAddPengalaman(this.newPeng)
        .then((response) => {
          this.alertSuccessInsert()
          window.location = '/edit-profile-pekerja'
          this.newPeng.posisi = ''
          this.newPeng.namaperusahaan = ''
          this.newPeng.mulaikerja = ''
          this.newPeng.selesaikerja = ''
          this.newPeng.deskripsi = ''
          // this.$bvCollapse.hide('add-collapse')
          this.getPengalaman(this.idpekerja)
        })
        .catch((err) => {
          alert(err)
        })
    },
    updatePengalaman (item) {
      this.$delete(item, 'lamakerja')
      this.onUpdatePengalaman(item)
        .then((response) => {
          this.alertSuccessInsert()
          window.location = '/edit-profile-pekerja'
          this.getPengalaman(this.idpekerja)
        })
        .catch((err) => {
          alert(err)
        })
    },
    deletePengalaman (idpengalaman) {
      if (confirm('Delete ?')) {
        this.onDeletePengalaman(idpengalaman)
          .then((response) => {
            this.alertSuccessDelete()
            window.location = '/edit-profile-pekerja'
            this.getPengalaman(this.idpekerja)
          })
          .catch((err) => {
            alert(err)
          })
      }
    },
    uploadPekerja (event) {
      this.image = event.target.files[0]
    },
    updatePekerjaImage () {
      const data = {
        idpekerja: this.idpekerja,
        imagepekerja: this.image
      }
      console.log(data)
      this.onUpdateImagePekerja(data).then((response) => {
        if (response.data.message === 'Image size is too big! Please upload another one with size <5mb') {
          this.alertSize()
        } else if (response.data.message === 'Image type must be JPG or JPEG') {
          this.alertValidation()
        } else {
          this.alertSuccess()
          window.location = '/edit-profile-pekerja'
        }
      })
    },
    AddPortofolio () {
      const dataImage = this.$refs.myVueDropzone.getAcceptedFiles()
      // console.log(dataImage[0])
      if (dataImage.length === 1) {
        this.newPor.image1 = dataImage[0]
        this.newPor.image2 = 'doc404.jpg'
        this.newPor.image3 = 'doc404.jpg'
      } else if (dataImage.length === 2) {
        this.newPor.image1 = dataImage[0]
        this.newPor.image2 = dataImage[1]
        this.newPor.image3 = 'doc404.jpg'
      } else if (dataImage.length === 3) {
        this.newPor.image1 = dataImage[0]
        this.newPor.image2 = dataImage[1]
        this.newPor.image3 = dataImage[2]
      }
      // console.log(this.newPor)
      this.onAddPortofolio(this.newPor).then((response) => {
        if (response.data.message === 'Image size is too big! Please upload another one with size <5mb') {
          this.alertSize()
        } else if (response.data.message === 'Image type must be JPG or JPEG') {
          this.alertValidation()
        } else {
          this.alertSuccess()
          window.location = '/edit-profile-pekerja'
        }
      })
    },
    uploadPorEditImg1 (event) {
      this.imageTampungan1 = event.target.files[0]
      // console.log(this.image)
    },
    uploadPorEditImg2 (event) {
      this.imageTampungan2 = event.target.files[0]
      // console.log(this.image2)
    },
    uploadPorEditImg3 (event) {
      this.imageTampungan3 = event.target.files[0]
      // console.log(this.imageTampungan3)
    },
    updateImagePortofolio1 (item) {
      const data = {
        idportofolio: item.idportofolio,
        image1: this.imageTampungan1
      }
      this.onUpdateImagePortofolio1(data).then((response) => {
        if (response.data.message === 'Image size is too big! Please upload another one with size <5mb') {
          this.alertSize()
        } else if (response.data.message === 'Image type must be JPG or JPEG') {
          this.alertValidation()
        } else {
          this.alertSuccess()
          window.location = '/edit-profile-pekerja'
        }
      })
    },
    updateImagePortofolio2 (item) {
      const data = {
        idportofolio: item.idportofolio,
        image2: this.imageTampungan2
      }
      this.onUpdateImagePortofolio2(data).then((response) => {
        if (response.data.message === 'Image size is too big! Please upload another one with size <5mb') {
          this.alertSize()
        } else if (response.data.message === 'Image type must be JPG or JPEG') {
          this.alertValidation()
        } else {
          this.alertSuccess()
          window.location = '/edit-profile-pekerja'
        }
      })
    },
    updateImagePortofolio3 (item) {
      const data = {
        idportofolio: item.idportofolio,
        image3: this.imageTampungan3
      }
      this.onUpdateImagePortofolio3(data).then((response) => {
        if (response.data.message === 'Image size is too big! Please upload another one with size <5mb') {
          this.alertSize()
        } else if (response.data.message === 'Image type must be JPG or JPEG') {
          this.alertValidation()
        } else {
          this.alertSuccess()
          window.location = '/edit-profile-pekerja'
        }
      })
    },
    updatePortofolio (item) {
      this.onUpdatePortofolio(item)
        .then((response) => {
          this.alertSuccessInsert()
          window.location = '/edit-profile-pekerja'
        })
        .catch((err) => {
          alert(err)
        })
    },
    deletePortofolio (id) {
      // console.log(id)
      if (confirm('Delete ?')) {
        this.onDeletePortofolio(id)
          .then((response) => {
            this.alertSuccessDelete()
            window.location = '/edit-profile-pekerja'
          })
          .catch((err) => {
            alert(err)
          })
      }
    },
    // Perekrut
    uploadPerekrut (event) {
      this.image = event.target.files[0]
    },
    updatePerekrutImage () {
      const data = {
        idperekrut: this.idperekrut,
        imageperekrut: this.image
      }
      this.onUpdateImagePerekrut(data).then((response) => {
        if (response.data.message === 'Image size is too big! Please upload another one with size <5mb') {
          this.alertSize()
        } else if (response.data.message === 'Image type must be JPG or JPEG') {
          this.alertValidation()
        } else {
          this.alertSuccess()
          window.location = '/edit-profile-perekrut'
        }
      })
    },
    updatePerusahaan () {
      console.log(this.dataperekrut)
      this.onUpdatePerusahaan(this.dataperekrut)
        .then((response) => {
          this.alertSuccessInsert()
          window.location = '/edit-profile-perekrut'
        })
        .catch((err) => {
          alert(err)
        })
    },
    ...mapActions({
      onUpdatePekerja: 'pekerja/updatePekerja',
      getPortofolio: 'pekerja/getPortofolio',
      getPengalaman: 'pekerja/getPengalaman',
      onAddPengalaman: 'pekerja/addPengalaman',
      onUpdatePengalaman: 'pekerja/updatePengalaman',
      onDeletePengalaman: 'pekerja/deletePengalaman',
      onAddPortofolio: 'pekerja/addPortofolio',
      onUpdatePortofolio: 'pekerja/updatePortofolio',
      onDeletePortofolio: 'pekerja/deletePortofolio',
      getProfile: 'perekrut/getProfileDetail',
      onUpdatePerusahaan: 'perekrut/updatePerusahaan',
      onUpdateImagePerekrut: 'perekrut/updateImage',
      onUpdateImagePekerja: 'pekerja/updateImage',
      onUpdateImagePortofolio1: 'pekerja/updateImagePortofolio1',
      onUpdateImagePortofolio2: 'pekerja/updateImagePortofolio2',
      onUpdateImagePortofolio3: 'pekerja/updateImagePortofolio3'
    })
  },
  computed: {
    ...mapGetters({
      dataz: 'pekerja/getDetailPekerja',
      dataPeng: 'pekerja/getPengalaman',
      dataPor: 'pekerja/getPortofolio',
      dataperekrut: 'perekrut/getProfile',
      dataImage: 'pekerja/getPortofolioImage'
    })
  },
  mounted () {
    this.getPortofolio(this.idpekerja)
    this.getPengalaman(this.idpekerja)
    this.getProfile(this.idperekrut)
  }
}
</script>
